img {
    position: relative;
    float: left;
    margin: 10px;
}

.subImages img {
    width: 20%;
}

.categories {
    position: relative;
    border: 1px solid #ddd;
    transition: border 0.3s ease-out;
    border-right: none;
    margin-bottom: 20px;
    -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    -moz-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 4 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.categories:hover {
    border: 1px solid #009688;
    border-right: none;
}

.subcategory {
    position: absolute;
    opacity: 0;
    width: 100%;
    left: 0;
    top: 0;
    height: 100%;
    border: 1px solid #009688;
    background-color: #fff;
    z-index: 2;
    border-left: none;
    -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    -moz-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    box-shadow: 4 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}

.subcategory a {
    border-right: none !important;
}

.parent a {
    position: relative;
    display: block;
    padding: 10px;
    z-index: 3;
    background-color: #fff;
    text-decoration: none;
    font-weight: 500;
    border-right: 1px solid #ddd;
}

.parent a:hover {
    background-color: #009688;
    color: #fff;
}

.parent a::after {
    background-color: #009688;
    color: #fff;
}

/** TODO: ADD MEDIA SECTION FOR DIFFERENT SCREENS **/
.site {
    padding-top: 20px;
    background-color: #fff;
    margin: 0 auto;
    overflow: hidden;
    padding: 10px;
}

.subcategory-small {
    display: none;
}

.product {
    margin-bottom: 10px;
}

.navbar {
    margin-bottom: 0;
}

.navbar-right {
    margin-right: 0 !important;
}

html, body {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
}
